.dashboard-container {
    max-width: 768px;
    margin: 0 auto;
    padding: 20px;
    background-color: #ebe07c;
    border-radius: 5px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
}

.user-info__title {
    text-align: center;
    font-size: 2rem;
    font-weight: 700;
    color: #494945;
}

.summary-article__title {
    font-size: 20px;
    font-weight: 600;
    margin: 8px 8px 0;
    padding: 0;
}

.user-info_username {
    font-size: 16px;
    font-weight: 500;
}

/* Style headings */
h2 {
    color: #333;
}

/* Style user info */
.user-info {
    margin-bottom: 20px;
}

/* Style activity section */
.activity-section {
    margin-top: 20px;
}

/* Style posted articles section */
.posted-articles {
    margin-top: 30px;
}

/* Style individual article */
.article {
    margin-bottom: 10px;
    background-color: #f7f7f7;
    border: 1px solid #ddd;
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
}

.posted-articles__title {
    margin-top: 16px;
    font-size: 1.5rem;
    font-weight: 600;
    color: #494945;
}

/* Style loading message */
.loading-message {
    font-style: italic;
    color: #777;
}

.summary-article__tags {
    font-size: 12px;
    background-color: #e6e3e3;
    width: fit-content;
    margin: 12px 8px;
    padding: 3px 6px;
    color: #000000;
    border: 1px solid #e6e3e3;
    border-radius: 8px;
}

.summary__tags {
    display: flex;
    flex-direction: row;
    gap: 0;
}

.summary_text {
    padding: 12px 24px;
    text-align: justify;
}

#retrieve-data-button {
        margin: 16px;
        background-color: #e4ff4c; /* Button background color */
        color: #3f3e3e; /* Text color */
        font-size: 16px;
        font-weight: 600;
        padding: 10px 20px; /* Padding around the button text */
        border: none; /* Remove button border */
        border-radius: 5px; /* Rounded corners */
        cursor: pointer; /* Change cursor to pointer on hover */
        transition: background-color 0.3s ease; /* Add a smooth color transition */
        box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    
    /* Style the button on hover */
}


#retrieve-data-button:hover {
    background-color: #c8e935; /* Change background color on hover */
}